@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Questrial&display=swap');

html,body{font-size:13px;min-width:320px;font-family: 'Questrial', sans-serif;}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-family: 'Questrial', sans-serif;;vertical-align:baseline;-webkit-font-smoothing: antialiased;}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}
body,input,textarea,select,button{font-size:1rem;line-height:1.3rem;font:inherit;-webkit-font-smoothing: antialiased;}
ol,ul{list-style:none}
blockquote,q{quotes:none}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}
table{border-collapse:collapse;border-spacing:0}
button{outline:none;cursor:pointer;}
a{text-decoration:none;}


h1{font-size:2rem;line-height:2.3rem}
h2{font-size:1.8rem;line-height:2.1rem}
h3{font-size:1.6rem;line-height:1.9rem}
h4{font-size:1.4rem;line-height:1.7rem}
h5{font-size:1.2rem;line-height:1.5rem}
h6{font-size:1.1rem;line-height:1.4rem}
p{font-size:1rem;line-height:1.3rem}